import { loadAsync } from 'jszip';

/**
 * Reads XLSX file in a browser.
 * @param  {file} file - A file being uploaded in the browser.
 * @return {Promise} Resolves to an object holding XLSX file entries.
 */
export default async function unpackXlsxFile(file) {
	const zip = await loadAsync(file);
  const files = [];
  zip.forEach((_, zipEntry) => {
    if (!zipEntry.dir) {
      files.push(zipEntry.name);
    }
  });
  const entries = {};
  await Promise.all(files.map(async (file_1) => {
    const content = await zip.file(file_1).async('string');
    return entries[file_1] = content;
  }));
  return entries;
}
